// // eslint-disable-next-line import/no-webpack-loader-syntax
// import {Utils} from 'worker-loader!./Utils'; 
export default () => {
	
	let timerInterval //, utils;// = Utils;

	if('function' === typeof importScripts) {
		onmessage = function (e) {
			//utils = this.importScripts('https://localhost:3000/ReactJsBatteryNotifier/src/Components/Utils.js');
			let data = e.data;
			console.log('data passed as : ', data)
			switch (data.cmd) {
				case 'start':
					startMonitoring(data);
					this.postMessage('WORKER STARTED: ' + data.msg);
					break;
				case 'stop':
					stopMonitoring(data);
					break;
				default:
					this.postMessage('Unknown command: ' + data.msg);
			};
		}

		const startMonitoring = function(data) {
			let count = 0;
			timerInterval = setInterval(() => {
				if (count === 60) {
					stopMonitoring({
					'cmd': 'stop',
					'msg': 'from Background worker'
					})
				}
				count++;
				console.log('Interval at : ' + getBatteryTimeText(Date.now()))
			}, 1000);
		}

		const stopMonitoring = function(data) {
			clearInterval(timerInterval);
			this.close(); // Terminates the worker.
			this.postMessage('WORKER STOPPED: ' + data.msg);
		}

		const getBatteryTimeText = function(timeTxt) {
			const dateVal = new Date(timeTxt)
			return dateVal.toLocaleDateString() + ' ' + dateVal.toLocaleTimeString();
		}
	
	}

}